@import '../../Variables/Variables.scss';

.servicesHome {
    position: relative;
    background-color: $gray;

    &::after {
        content: "";
        position: absolute;
        top: -5rem;
        width: 70%;
        height: 6rem;
        display: block;
        background-color: $gray;

        @include breakpoint(tablet-landscape) {
            width: 100%;
            height: 3rem;
            top: -2rem;
            right: 30%;
        }
    }

    &__serviceBox {
        position: relative;
        background-color: $light-gray;
        padding: 5rem;

        @include breakpoint(tablet-landscape) {
            padding: 3rem;
        }

        h4, p {
            color:$dark-gray;
        }

        h4 {
            width: 50%;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 1.2rem;
            margin-bottom: 2rem;
        }

        &::after {
            content: "";
            position: absolute;
            display: block;
            background-color: $light-gray;
            bottom: 0;
            left: 100%;
            width: 70%;
            height: 46rem;
        }

        &::before {
            content: "";
            position: absolute;
            display: block;
            background-color: $light-gray;
            bottom: 0;
            right: 100%;
            width: 70%;
            height: 16rem;
        }

        .row div:not(:last-of-type) {
            margin-bottom: 3rem;
        }
    }
}