@import '../../Variables/Variables.scss';

.navbar {

    &__nav {
        padding: 2rem 8rem;

        @include breakpoint(tablet-landscape) {
            padding: 3rem;
        }

        @include breakpoint(tablet-large) {
            padding: 2rem;
        }
    }

    &__brand {
        img {
            width: 100%;

        }

        @include breakpoint(tablet-large) {
            width: 70%;
            padding: 1rem;
        }
        
    }

    &__links {

        li {
            text-transform: uppercase;
            
            &:not(:last-of-type) {
                margin-right: 2rem;

                @include breakpoint(tablet-landscape) {
                    margin-right: 0.5rem;
                }
            }

            a {
                font-size: 0.85rem;
                transition: color 300ms ease-in-out;

                &:hover {
                    color: $orange !important;
                }

                @include breakpoint(tablet-landscape) {
                    font-size: 0.7rem;
                }
            }

        }

        @include breakpoint(tablet-landscape) {
            padding: unset;
        }

        @include breakpoint(tablet-large) {
            padding: 3rem;
            background-color: $dark-gray;
        }
    }
}