@import '../../Variables/Variables.scss';

.aboutUsHome {
    background-color: $dark-gray;

    .row {
        margin-bottom: 0;

        &:not(:last-child) {
            margin-bottom: 6rem;
        }
    }

    &__stats {
        p {

            &:not(:last-child) {
                font-size: 5rem;
                color: $orange;
                font-weight: 900;
                margin-bottom: 0;

                @include breakpoint(tablet-landscape) {
                    font-size: 4rem;
                }
            }
        }
    }

    &__desc {
        color: $light-gray;

        @include breakpoint(tablet-landscape) {
            padding-top: 2rem;
        }
    }
}