@import './Variables/Variables.scss';

.App {
    overflow: hidden;
    position: relative;
}

* {
    font-family: 'Lato', sans-serif;
    color: $light-gray;
}

a, a:visited, a:hover {
    text-decoration: none;
}

.padding--8-0 {
    padding: 8rem 0;

    @include breakpoint(tablet-landscape) {
        padding: 4rem 2rem;
    }
}

.c-orange {
    color: $orange;
}

// Buttons

.button {
    display: inline-block;
    background-color: $color-white;
    color: $dark-gray;
    margin-top: 2rem;
    border-radius: 0;
    padding: 1rem 2rem;
    text-transform: uppercase;
    letter-spacing: 0.125rem;
    font-size: 1rem;
    transition: background-color 300ms ease-in-out;

    &:hover {
        background-color: $orange;
        color: $color-white;
    }
}

// Titles

.title {

    &__sm-title {
        h4 {
            font-size: 0.75rem;
            color: $orange;
        }

        &::after {
            content: "";
            display: block;
            background-color: $orange;
            width: 100%;
            height: 0.0625rem;
        }
    }

    &__lg-title {
        width: 80%;

        h2 {
            margin-bottom: 0;
            color: $color-white;
            font-size: 3rem;
            text-transform: lowercase;

            @include breakpoint(tablet-landscape) {
                margin-top: 2rem;
            }
        }
    }
}

// Phone 

.phone {
    margin-left: 150px;

    a {
        font-size: 1.5rem;
        font-weight: 700;
        color: $orange;
        transition: color 300ms ease-in-out;

        &:hover {
            color: $color-white;
        }

    }

    @include breakpoint(tablet-landscape) {
        display: none;
    }
}

// back to top

#button {
    display: inline-block;
    background-color: $light-gray;
    width: 50px;
    height: 50px;
    text-align: center;
    border: 0;
    border-radius: 0;
    position: fixed;
    bottom: 30px;
    right: 30px;
    transition: background-color .3s, 
    opacity .5s, visibility .5s;
    opacity: 0;
    visibility: hidden;
    z-index: 1000;
  
    &::after {
      content: "";
      position: absolute;
      left: 13px;
      z-index: 11;
      display: block;
      width: 25px;
      height: 25px;
      border-top: 2px solid $dark-gray;
      border-left: 2px solid $dark-gray;
      top: 20px;
      transform: rotate(45deg);
    }
  
    &:focus {
      outline: 0;
    }
  }
  
  #button:hover {
    cursor: pointer;
    background-color:$dark-gray;
  
    &::after {
      border-top: 2px solid $light-gray;
      border-left: 2px solid $light-gray;
    }
  }
  #button:active {
    background-color:$dark-gray;
    border: 0;
  }
  #button.show {
    opacity: 1;
    visibility: visible;
  }