@import '../../Variables/Variables.scss';

.footer {
    background-color: $gray;
    padding-bottom: 1rem;

    &__content {
        p {
            margin-top: 2rem;

            &:not(:last-of-type) {
                width: 80%;
                margin: 0;
                font-size: 1.5rem;
                font-weight: 700;    
            }
           
        }
        .phone {
            margin-top: 3.3rem;
            margin-left: 0;

            a {
                color:$color-white;

                &:hover {
                    color: $orange;
                }
            }
           
        }
    }
}