@import '../../Variables/Variables.scss';

.hero {
    width: 100%;
    height: 100vh; 
    background-repeat: no-repeat;
    background-size:cover;

    @include breakpoint(tablet) {
        height: unset;
    }

    &__content {
        padding: 8rem;
        width: 90%;

        @include breakpoint(tablet-landscape) {
            padding: 3rem;
        }
    }

    &__title {
        width: 50%;
        font-size: 5rem;
        padding-bottom: 2rem;
        text-transform: uppercase;
        color: $color-white;
        font-weight: 700;

        @include breakpoint(desktop-default) {
            font-size: 3.5rem;
        }
        
        @include breakpoint(tablet) {
            width: 100%;
            font-size: 2rem;
        }
    }

    &__desc {
        width: 35% !important;
        font-size: 1.3rem;
        width: 70%;
        line-height: 1.7;
        color: $color-white;

        @include breakpoint(tablet-landscape){
            width: 100% !important;
        }
    }
}