@import '../../Variables/Variables.scss';

.portfolioHome {
    background-color: $light-gray;

    &__content-left {
        position: relative;
        background-color: $dark-gray;
        padding: 4rem;

        max-height: 34.8rem;

        @include breakpoint(tablet-landscape) {
            max-height: unset;
            padding: 3rem;
        }

        .title__lg-title {
            width: 100%;
            padding: 2rem 0;
        }

        &::before {
            content: "";
            position: absolute;
            right: 100%;
            bottom:0;
            width: 100%;
            height: 6rem;
            display: block;
            background-color: $gray;
        }
    }

    .carousel-next, .carousel-prev {
        display: none;
        position: absolute;
        top: 27.5rem;
        z-index: 1;
        background: $dark-gray;
        padding: 1.2rem;
        line-height: 1rem;
        transition: background-color 300ms ease-in-out;

        &:hover {
            background-color: $orange;
        }
    }

    .carousel-prev {
        left: 4.5rem;
    }

    .carousel-next {
        left: 10.5rem;
    }

    .max-height {
        position: relative;
        max-height: 35.125rem;
        overflow: hidden;
    }
} 